/**
 * ÊÄßËÉΩÁõëÊéßÂ∑•ÂÖ∑
 * ÁõëÊéßÂ∫îÁî®ÊÄßËÉΩÊåáÊ†áÔºåÂåÖÊã¨Ê∏≤ÊüìÊÄßËÉΩ„ÄÅÁΩëÁªúËØ∑Ê±Ç„ÄÅÂÜÖÂ≠ò‰ΩøÁî®Á≠â
 */

import AsyncStorage from '@react-native-async-storage/async-storage';
import { Platform, InteractionManager, AppState, AppStateStatus } from 'react-native';
import { deviceInfoManager, PerformanceMetrics } from './deviceInfo';

// Â£∞ÊòéÂÖ®Â±ÄperformanceÂØπË±°ÔºàReact NativeÁéØÂ¢ÉÔºâ
declare global {
  const performance: {
    now(): number;
    memory?: {
      usedJSHeapSize: number;
      totalJSHeapSize: number;
      jsHeapSizeLimit: number;
    };
  };
}

export interface PerformanceMetric {
  id: string;
  name: string;
  type: 'render' | 'network' | 'memory' | 'user_interaction' | 'custom';
  startTime: number;
  endTime?: number;
  duration?: number;
  metadata?: Record<string, any>;
  timestamp: Date;
}

export interface NetworkMetric extends PerformanceMetric {
  type: 'network';
  url: string;
  method: string;
  status?: number;
  responseSize?: number;
  requestSize?: number;
}

export interface RenderMetric extends PerformanceMetric {
  type: 'render';
  componentName: string;
  renderCount?: number;
  props?: Record<string, any>;
}

export interface MemoryMetric extends PerformanceMetric {
  type: 'memory';
  usedJSHeapSize?: number;
  totalJSHeapSize?: number;
  jsHeapSizeLimit?: number;
}

export interface UserInteractionMetric extends PerformanceMetric {
  type: 'user_interaction';
  action: string;
  target: string;
  coordinates?: { x: number; y: number };
}

export interface PerformanceBenchmark {
  name: string;
  startTime: number;
  endTime?: number;
  duration?: number;
  metadata?: Record<string, any>;
}

export interface MemoryWarning {
  timestamp: number;
  memoryUsage: number;
  threshold: number;
  severity: 'low' | 'medium' | 'high' | 'critical';
}

export interface PerformanceAlert {
  type: 'memory' | 'cpu' | 'battery' | 'network' | 'render';
  severity: 'warning' | 'error' | 'critical';
  message: string;
  timestamp: number;
  metrics: any;
}

/**
 * ÊÄßËÉΩÁõëÊéßÂô®Á±ª
 */
export class PerformanceMonitor {
  private static instance: PerformanceMonitor;
  private metrics: PerformanceMetric[] = [];
  private activeMetrics: Map<string, PerformanceMetric> = new Map();
  private isEnabled: boolean = true;
  private maxMetrics: number = 1000;
  private benchmarks: Map<string, PerformanceBenchmark> = new Map();
  private memoryWarnings: MemoryWarning[] = [];
  private performanceAlerts: PerformanceAlert[] = [];
  private monitoringInterval: ReturnType<typeof setInterval> | null = null;
  private isMonitoring = false;
  
  // ÊÄßËÉΩÈòàÂÄºÈÖçÁΩÆ
  private thresholds = {
    memory: {
      warning: 70,    // 70%ÂÜÖÂ≠ò‰ΩøÁî®ÁéáË≠¶Âëä
      error: 85,      // 85%ÂÜÖÂ≠ò‰ΩøÁî®ÁéáÈîôËØØ
      critical: 95,   // 95%ÂÜÖÂ≠ò‰ΩøÁî®Áéá‰∏•Èáç
    },
    cpu: {
      warning: 70,
      error: 85,
      critical: 95,
    },
    battery: {
      warning: 20,    // 20%ÁîµÈáèË≠¶Âëä
      error: 10,      // 10%ÁîµÈáèÈîôËØØ
      critical: 5,    // 5%ÁîµÈáè‰∏•Èáç
    },
    network: {
      warning: 1000,  // 1ÁßíÁΩëÁªúÂª∂ËøüË≠¶Âëä
      error: 3000,    // 3ÁßíÁΩëÁªúÂª∂ËøüÈîôËØØ
      critical: 5000, // 5ÁßíÁΩëÁªúÂª∂Ëøü‰∏•Èáç
    },
    render: {
      warning: 16,    // 16msÊ∏≤ÊüìÊó∂Èó¥Ë≠¶Âëä
      error: 32,      // 32msÊ∏≤ÊüìÊó∂Èó¥ÈîôËØØ
      critical: 50,   // 50msÊ∏≤ÊüìÊó∂Èó¥‰∏•Èáç
    },
  };

  private constructor() {
    this.loadSettings();
    this.setupAppStateListener();
  }

  static getInstance(): PerformanceMonitor {
    if (!PerformanceMonitor.instance) {
      PerformanceMonitor.instance = new PerformanceMonitor();
    }
    return PerformanceMonitor.instance;
  }

  /**
   * ÂêØÁî®/Á¶ÅÁî®ÊÄßËÉΩÁõëÊéß
   */
  setEnabled(enabled: boolean): void {
    this.isEnabled = enabled;
  }

  /**
   * ÂºÄÂßãÊÄßËÉΩÊµãÈáè
   */
  startMeasure(
    id: string,
    name: string,
    type: PerformanceMetric['type'],
    metadata?: Record<string, any>
  ): void {
    if (!this.isEnabled) return;

    const metric: PerformanceMetric = {
      id,
      name,
      type,
      startTime: performance.now(),
      metadata,
      timestamp: new Date(),
    };

    this.activeMetrics.set(id, metric);
  }

  /**
   * ÁªìÊùüÊÄßËÉΩÊµãÈáè
   */
  endMeasure(id: string, additionalMetadata?: Record<string, any>): void {
    if (!this.isEnabled) return;

    const metric = this.activeMetrics.get(id);
    if (!metric) {
      console.warn(`Performance metric with id "${id}" not found`);
      return;
    }

    metric.endTime = performance.now();
    metric.duration = metric.endTime - metric.startTime;
    
    if (additionalMetadata) {
      metric.metadata = { ...metric.metadata, ...additionalMetadata };
    }

    this.activeMetrics.delete(id);
    this.addMetric(metric);
  }

  /**
   * ËÆ∞ÂΩïÁΩëÁªúËØ∑Ê±ÇÊÄßËÉΩ
   */
  recordNetworkRequest(
    url: string,
    method: string,
    startTime: number,
    endTime: number,
    status?: number,
    responseSize?: number,
    requestSize?: number
  ): void {
    if (!this.isEnabled) return;

    const metric: NetworkMetric = {
      id: `network_${Date.now()}_${Math.random()}`,
      name: `${method} ${url}`,
      type: 'network',
      url,
      method,
      status,
      responseSize,
      requestSize,
      startTime,
      endTime,
      duration: endTime - startTime,
      timestamp: new Date(),
    };

    this.addMetric(metric);
  }

  /**
   * ËÆ∞ÂΩïÁªÑ‰ª∂Ê∏≤ÊüìÊÄßËÉΩ
   */
  recordRender(
    componentName: string,
    duration: number,
    renderCount?: number,
    props?: Record<string, any>
  ): void {
    if (!this.isEnabled) return;

    const metric: RenderMetric = {
      id: `render_${componentName}_${Date.now()}`,
      name: `Render ${componentName}`,
      type: 'render',
      componentName,
      renderCount,
      props,
      startTime: performance.now() - duration,
      endTime: performance.now(),
      duration,
      timestamp: new Date(),
    };

    this.addMetric(metric);
  }

  /**
   * ËÆ∞ÂΩïÂÜÖÂ≠ò‰ΩøÁî®ÊÉÖÂÜµ
   */
  recordMemoryUsage(): void {
    if (!this.isEnabled) return;

    // React Native‰∏≠Ëé∑ÂèñÂÜÖÂ≠ò‰ø°ÊÅØÁöÑÊñπÊ≥ïÊúâÈôê
    // ËøôÈáåÊèê‰æõ‰∏Ä‰∏™Âü∫Á°ÄÊ°ÜÊû∂ÔºåÂÆûÈôÖÂÆûÁé∞ÂèØËÉΩÈúÄË¶ÅÂéüÁîüÊ®°ÂùóÊîØÊåÅ
    const metric: MemoryMetric = {
      id: `memory_${Date.now()}`,
      name: 'Memory Usage',
      type: 'memory',
      startTime: performance.now(),
      endTime: performance.now(),
      duration: 0,
      timestamp: new Date(),
      // Âú®WebÁéØÂ¢É‰∏≠ÂèØ‰ª•‰ΩøÁî® performance.memory
      ...(performance.memory && {
        usedJSHeapSize: performance.memory.usedJSHeapSize,
        totalJSHeapSize: performance.memory.totalJSHeapSize,
        jsHeapSizeLimit: performance.memory.jsHeapSizeLimit,
      }),
    };

    this.addMetric(metric);
  }

  /**
   * ËÆ∞ÂΩïÁî®Êà∑‰∫§‰∫í
   */
  recordUserInteraction(
    action: string,
    target: string,
    coordinates?: { x: number; y: number },
    metadata?: Record<string, any>
  ): void {
    if (!this.isEnabled) return;

    const metric: UserInteractionMetric = {
      id: `interaction_${Date.now()}_${Math.random()}`,
      name: `${action} on ${target}`,
      type: 'user_interaction',
      action,
      target,
      coordinates,
      startTime: performance.now(),
      endTime: performance.now(),
      duration: 0,
      timestamp: new Date(),
      metadata,
    };

    this.addMetric(metric);
  }

  /**
   * Ê∑ªÂä†Ëá™ÂÆö‰πâÊåáÊ†á
   */
  recordCustomMetric(
    name: string,
    duration: number,
    metadata?: Record<string, any>
  ): void {
    if (!this.isEnabled) return;

    const metric: PerformanceMetric = {
      id: `custom_${Date.now()}_${Math.random()}`,
      name,
      type: 'custom',
      startTime: performance.now() - duration,
      endTime: performance.now(),
      duration,
      timestamp: new Date(),
      metadata,
    };

    this.addMetric(metric);
  }

  /**
   * Ê∑ªÂä†ÊåáÊ†áÂà∞ÈõÜÂêà
   */
  private addMetric(metric: PerformanceMetric): void {
    this.metrics.push(metric);

    // ÈôêÂà∂ÊåáÊ†áÊï∞ÈáèÔºåÈÅøÂÖçÂÜÖÂ≠òÊ≥ÑÊºè
    if (this.metrics.length > this.maxMetrics) {
      this.metrics = this.metrics.slice(-this.maxMetrics);
    }

    // Âú®ÂºÄÂèëÁéØÂ¢É‰∏≠ËæìÂá∫ÊÄßËÉΩË≠¶Âëä
    if (__DEV__ && this.shouldWarnAboutPerformance(metric)) {
      console.warn(`Performance warning: ${metric.name} took ${metric.duration?.toFixed(2)}ms`);
    }
  }

  /**
   * Âà§Êñ≠ÊòØÂê¶Â∫îËØ•ÂèëÂá∫ÊÄßËÉΩË≠¶Âëä
   */
  private shouldWarnAboutPerformance(metric: PerformanceMetric): boolean {
    if (!metric.duration) return false;

    switch (metric.type) {
      case 'render':
        return metric.duration > 16; // Ë∂ÖËøá‰∏ÄÂ∏ßÁöÑÊó∂Èó¥
      case 'network':
        return metric.duration > 5000; // Ë∂ÖËøá5Áßí
      case 'user_interaction':
        return metric.duration > 100; // Ë∂ÖËøá100ms
      default:
        return metric.duration > 1000; // Ë∂ÖËøá1Áßí
    }
  }

  /**
   * Ëé∑ÂèñÊÄßËÉΩÁªüËÆ°
   */
  getStats(): {
    total: number;
    byType: Record<string, number>;
    averageDuration: Record<string, number>;
    slowestOperations: PerformanceMetric[];
    recentMetrics: PerformanceMetric[];
  } {
    const byType: Record<string, number> = {};
    const durationByType: Record<string, number[]> = {};
    
    this.metrics.forEach(metric => {
      byType[metric.type] = (byType[metric.type] || 0) + 1;
      
      if (metric.duration) {
        if (!durationByType[metric.type]) {
          durationByType[metric.type] = [];
        }
        durationByType[metric.type].push(metric.duration);
      }
    });

    const averageDuration: Record<string, number> = {};
    Object.keys(durationByType).forEach(type => {
      const durations = durationByType[type];
      averageDuration[type] = durations.reduce((a, b) => a + b, 0) / durations.length;
    });

    // Ëé∑ÂèñÊúÄÊÖ¢ÁöÑ10‰∏™Êìç‰Ωú
    const slowestOperations = [...this.metrics]
      .filter(m => m.duration)
      .sort((a, b) => (b.duration || 0) - (a.duration || 0))
      .slice(0, 10);

    // Ëé∑ÂèñÊúÄËøëÁöÑ50‰∏™ÊåáÊ†á
    const recentMetrics = this.metrics.slice(-50);

    return {
      total: this.metrics.length,
      byType,
      averageDuration,
      slowestOperations,
      recentMetrics,
    };
  }

  /**
   * Ëé∑ÂèñÁΩëÁªúÊÄßËÉΩÁªüËÆ°
   */
  getNetworkStats(): {
    totalRequests: number;
    averageResponseTime: number;
    successRate: number;
    slowestRequests: NetworkMetric[];
    errorRequests: NetworkMetric[];
  } {
    const networkMetrics = this.metrics.filter(m => m.type === 'network') as NetworkMetric[];
    
    const totalRequests = networkMetrics.length;
    const successfulRequests = networkMetrics.filter(m => m.status && m.status < 400).length;
    const errorRequests = networkMetrics.filter(m => m.status && m.status >= 400);
    
    const averageResponseTime = networkMetrics.reduce((sum, m) => sum + (m.duration || 0), 0) / totalRequests;
    const successRate = totalRequests > 0 ? (successfulRequests / totalRequests) * 100 : 0;
    
    const slowestRequests = [...networkMetrics]
      .sort((a, b) => (b.duration || 0) - (a.duration || 0))
      .slice(0, 10);

    return {
      totalRequests,
      averageResponseTime,
      successRate,
      slowestRequests,
      errorRequests,
    };
  }

  /**
   * Ê∏ÖÈô§ÊâÄÊúâÊåáÊ†á
   */
  clearMetrics(): void {
    this.metrics = [];
    this.activeMetrics.clear();
  }

  /**
   * ÂØºÂá∫ÊÄßËÉΩÊï∞ÊçÆ
   */
  exportData(): {
    timestamp: Date;
    metrics: PerformanceMetric[];
    stats: any;
    networkStats: any;
  } {
    return {
      timestamp: new Date(),
      metrics: [...this.metrics],
      stats: this.getStats(),
      networkStats: this.getNetworkStats(),
    };
  }

  /**
   * ‰øùÂ≠òËÆæÁΩÆ
   */
  private async saveSettings(): Promise<void> {
    try {
      await AsyncStorage.setItem('performance_monitor_settings', JSON.stringify({
        isEnabled: this.isEnabled,
        maxMetrics: this.maxMetrics,
      }));
    } catch (error) {
      console.error('Failed to save performance monitor settings:', error);
    }
  }

  /**
   * Âä†ËΩΩËÆæÁΩÆ
   */
  private async loadSettings(): Promise<void> {
    try {
      const settings = await AsyncStorage.getItem('performance_monitor_settings');
      if (settings) {
        const parsed = JSON.parse(settings);
        this.isEnabled = parsed.isEnabled ?? true;
        this.maxMetrics = parsed.maxMetrics ?? 1000;
      }
    } catch (error) {
      console.error('Failed to load performance monitor settings:', error);
    }
  }

  /**
   * ËÆæÁΩÆÊúÄÂ§ßÊåáÊ†áÊï∞Èáè
   */
  setMaxMetrics(max: number): void {
    this.maxMetrics = max;
    this.saveSettings();
  }

  /**
   * ÂºÄÂßãÊÄßËÉΩÁõëÊéß
   */
  startMonitoring(interval: number = 5000): void {
    if (this.isMonitoring) {
      console.warn('ÊÄßËÉΩÁõëÊéßÂ∑≤Âú®ËøêË°å‰∏≠');
      return;
    }

    this.isMonitoring = true;
    console.log('üîç ÂºÄÂßãÊÄßËÉΩÁõëÊéß...');

    this.monitoringInterval = setInterval(async () => {
      try {
        await this.collectPerformanceData();
      } catch (error) {
        console.error('ÊÄßËÉΩÊï∞ÊçÆÊî∂ÈõÜÂ§±Ë¥•:', error);
      }
    }, interval);
  }

  /**
   * ÂÅúÊ≠¢ÊÄßËÉΩÁõëÊéß
   */
  stopMonitoring(): void {
    if (this.monitoringInterval) {
      clearInterval(this.monitoringInterval);
      this.monitoringInterval = null;
    }
    this.isMonitoring = false;
    console.log('‚èπÔ∏è ÊÄßËÉΩÁõëÊéßÂ∑≤ÂÅúÊ≠¢');
  }

  /**
   * Êî∂ÈõÜÊÄßËÉΩÊï∞ÊçÆ
   */
  private async collectPerformanceData(): Promise<void> {
    try {
      const metrics = await deviceInfoManager.getCurrentPerformanceMetrics();
      this.analyzePerformanceMetrics(metrics);
    } catch (error) {
      console.error('Êî∂ÈõÜÊÄßËÉΩÊï∞ÊçÆÂ§±Ë¥•:', error);
    }
  }

  /**
   * ÂàÜÊûêÊÄßËÉΩÊåáÊ†áÂπ∂ÁîüÊàêË≠¶Âëä
   */
  private analyzePerformanceMetrics(metrics: PerformanceMetrics): void {
    // Ê£ÄÊü•ÂÜÖÂ≠ò‰ΩøÁî®
    this.checkMemoryUsage(metrics.memoryUsage);
    
    // Ê£ÄÊü•CPU‰ΩøÁî®
    this.checkCpuUsage(metrics.cpuUsage);
    
    // Ê£ÄÊü•ÁîµÊ±†Ê∂àËÄó
    this.checkBatteryDrain(metrics.batteryDrain);
    
    // Ê£ÄÊü•ÁΩëÁªúÂª∂Ëøü
    this.checkNetworkLatency(metrics.networkLatency);
    
    // Ê£ÄÊü•Ê∏≤ÊüìÊÄßËÉΩ
    this.checkRenderTime(metrics.renderTime);
  }

  /**
   * Ê£ÄÊü•ÂÜÖÂ≠ò‰ΩøÁî®ÊÉÖÂÜµ
   */
  private checkMemoryUsage(memoryUsage: PerformanceMetrics['memoryUsage']): void {
    const percentage = memoryUsage.percentage;
    let severity: MemoryWarning['severity'] = 'low';
    
    if (percentage >= this.thresholds.memory.critical) {
      severity = 'critical';
      this.addPerformanceAlert('memory', 'critical', `ÂÜÖÂ≠ò‰ΩøÁî®ÁéáËææÂà∞${percentage.toFixed(1)}%ÔºåÂ∫îÁî®ÂèØËÉΩÂ¥©Ê∫É`, memoryUsage);
    } else if (percentage >= this.thresholds.memory.error) {
      severity = 'high';
      this.addPerformanceAlert('memory', 'error', `ÂÜÖÂ≠ò‰ΩøÁî®ÁéáËøáÈ´òÔºö${percentage.toFixed(1)}%`, memoryUsage);
    } else if (percentage >= this.thresholds.memory.warning) {
      severity = 'medium';
      this.addPerformanceAlert('memory', 'warning', `ÂÜÖÂ≠ò‰ΩøÁî®ÁéáËæÉÈ´òÔºö${percentage.toFixed(1)}%`, memoryUsage);
    }

    if (severity !== 'low') {
      this.memoryWarnings.push({
        timestamp: Date.now(),
        memoryUsage: percentage,
        threshold: this.getThresholdForSeverity('memory', severity),
        severity,
      });
    }
  }

  /**
   * Ê£ÄÊü•CPU‰ΩøÁî®ÊÉÖÂÜµ
   */
  private checkCpuUsage(cpuUsage: number): void {
    if (cpuUsage >= this.thresholds.cpu.critical) {
      this.addPerformanceAlert('cpu', 'critical', `CPU‰ΩøÁî®ÁéáËøáÈ´òÔºö${cpuUsage.toFixed(1)}%`, { cpuUsage });
    } else if (cpuUsage >= this.thresholds.cpu.error) {
      this.addPerformanceAlert('cpu', 'error', `CPU‰ΩøÁî®ÁéáËæÉÈ´òÔºö${cpuUsage.toFixed(1)}%`, { cpuUsage });
    } else if (cpuUsage >= this.thresholds.cpu.warning) {
      this.addPerformanceAlert('cpu', 'warning', `CPU‰ΩøÁî®ÁéáÂÅèÈ´òÔºö${cpuUsage.toFixed(1)}%`, { cpuUsage });
    }
  }

  /**
   * Ê£ÄÊü•ÁîµÊ±†Ê∂àËÄó
   */
  private checkBatteryDrain(batteryDrain: number): void {
    const batteryLevel = (1 - batteryDrain) * 100;
    
    if (batteryLevel <= this.thresholds.battery.critical) {
      this.addPerformanceAlert('battery', 'critical', `ÁîµÊ±†ÁîµÈáè‰∏•Èáç‰∏çË∂≥Ôºö${batteryLevel.toFixed(1)}%`, { batteryLevel });
    } else if (batteryLevel <= this.thresholds.battery.error) {
      this.addPerformanceAlert('battery', 'error', `ÁîµÊ±†ÁîµÈáè‰∏çË∂≥Ôºö${batteryLevel.toFixed(1)}%`, { batteryLevel });
    } else if (batteryLevel <= this.thresholds.battery.warning) {
      this.addPerformanceAlert('battery', 'warning', `ÁîµÊ±†ÁîµÈáèËæÉ‰ΩéÔºö${batteryLevel.toFixed(1)}%`, { batteryLevel });
    }
  }

  /**
   * Ê£ÄÊü•ÁΩëÁªúÂª∂Ëøü
   */
  private checkNetworkLatency(networkLatency: number): void {
    if (networkLatency < 0) return; // ÁΩëÁªú‰∏çÂèØÁî®

    if (networkLatency >= this.thresholds.network.critical) {
      this.addPerformanceAlert('network', 'critical', `ÁΩëÁªúÂª∂ËøüËøáÈ´òÔºö${networkLatency}ms`, { networkLatency });
    } else if (networkLatency >= this.thresholds.network.error) {
      this.addPerformanceAlert('network', 'error', `ÁΩëÁªúÂª∂ËøüËæÉÈ´òÔºö${networkLatency}ms`, { networkLatency });
    } else if (networkLatency >= this.thresholds.network.warning) {
      this.addPerformanceAlert('network', 'warning', `ÁΩëÁªúÂª∂ËøüÂÅèÈ´òÔºö${networkLatency}ms`, { networkLatency });
    }
  }

  /**
   * Ê£ÄÊü•Ê∏≤ÊüìÊó∂Èó¥
   */
  private checkRenderTime(renderTime: number): void {
    if (renderTime >= this.thresholds.render.critical) {
      this.addPerformanceAlert('render', 'critical', `Ê∏≤ÊüìÊó∂Èó¥ËøáÈïøÔºö${renderTime.toFixed(1)}ms`, { renderTime });
    } else if (renderTime >= this.thresholds.render.error) {
      this.addPerformanceAlert('render', 'error', `Ê∏≤ÊüìÊó∂Èó¥ËæÉÈïøÔºö${renderTime.toFixed(1)}ms`, { renderTime });
    } else if (renderTime >= this.thresholds.render.warning) {
      this.addPerformanceAlert('render', 'warning', `Ê∏≤ÊüìÊó∂Èó¥ÂÅèÈïøÔºö${renderTime.toFixed(1)}ms`, { renderTime });
    }
  }

  /**
   * Ê∑ªÂä†ÊÄßËÉΩË≠¶Âëä
   */
  private addPerformanceAlert(
    type: PerformanceAlert['type'],
    severity: PerformanceAlert['severity'],
    message: string,
    metrics: any
  ): void {
    const alert: PerformanceAlert = {
      type,
      severity,
      message,
      timestamp: Date.now(),
      metrics,
    };

    this.performanceAlerts.push(alert);
    console.warn(`‚ö†Ô∏è ÊÄßËÉΩË≠¶Âëä [${severity.toUpperCase()}]: ${message}`);

    // ‰øùÊåÅË≠¶ÂëäÂàóË°®Â§ßÂ∞è
    if (this.performanceAlerts.length > 100) {
      this.performanceAlerts = this.performanceAlerts.slice(-50);
    }
  }

  /**
   * Ëé∑Âèñ‰∏•ÈáçÁ®ãÂ∫¶ÂØπÂ∫îÁöÑÈòàÂÄº
   */
  private getThresholdForSeverity(type: keyof typeof this.thresholds, severity: string): number {
    const thresholds = this.thresholds[type];
    switch (severity) {
      case 'critical': return thresholds.critical;
      case 'high': return thresholds.error;
      case 'medium': return thresholds.warning;
      default: return 0;
    }
  }

  /**
   * ÂºÄÂßãÊÄßËÉΩÂü∫ÂáÜÊµãËØï
   */
  startBenchmark(name: string, metadata?: Record<string, any>): void {
    const benchmark: PerformanceBenchmark = {
      name,
      startTime: Date.now(),
      metadata,
    };
    
    this.benchmarks.set(name, benchmark);
    console.log(`üèÅ ÂºÄÂßãÂü∫ÂáÜÊµãËØï: ${name}`);
  }

  /**
   * ÁªìÊùüÊÄßËÉΩÂü∫ÂáÜÊµãËØï
   */
  endBenchmark(name: string): PerformanceBenchmark | null {
    const benchmark = this.benchmarks.get(name);
    if (!benchmark) {
      console.warn(`Âü∫ÂáÜÊµãËØï "${name}" ‰∏çÂ≠òÂú®`);
      return null;
    }

    benchmark.endTime = Date.now();
    benchmark.duration = benchmark.endTime - benchmark.startTime;
    
    console.log(`üèÜ Âü∫ÂáÜÊµãËØïÂÆåÊàê: ${name} - ${benchmark.duration}ms`);
    return benchmark;
  }

  /**
   * ÊµãËØïÂ∫îÁî®ÂêØÂä®ÊÄßËÉΩ
   */
  async testAppStartupPerformance(): Promise<{
    coldStart: number;
    warmStart: number;
    jsLoad: number;
    firstRender: number;
  }> {
    return new Promise((resolve) => {
      const startTime = Date.now();
      
      // ÊµãËØïJSÂä†ËΩΩÊó∂Èó¥
      InteractionManager.runAfterInteractions(() => {
        const jsLoadTime = Date.now() - startTime;
        
        // ÊµãËØïÈ¶ñÊ¨°Ê∏≤ÊüìÊó∂Èó¥
        requestAnimationFrame(() => {
          const firstRenderTime = Date.now() - startTime;
          
          resolve({
            coldStart: startTime,
            warmStart: Date.now() - startTime,
            jsLoad: jsLoadTime,
            firstRender: firstRenderTime,
          });
        });
      });
    });
  }

  /**
   * ÊµãËØïÂÜÖÂ≠òÊ≥ÑÊºè
   */
  async testMemoryLeaks(iterations: number = 10): Promise<{
    initialMemory: number;
    finalMemory: number;
    memoryGrowth: number;
    averageGrowthPerIteration: number;
  }> {
    const initialMetrics = await deviceInfoManager.getCurrentPerformanceMetrics();
    const initialMemory = initialMetrics.memoryUsage.used;

    // Ê®°ÊãüÂÜÖÂ≠ò‰ΩøÁî®Êìç‰Ωú
    for (let i = 0; i < iterations; i++) {
      // ÂàõÂª∫‰∏Ä‰∫õ‰∏¥Êó∂ÂØπË±°Êù•Ê®°ÊãüÂÜÖÂ≠ò‰ΩøÁî®
      const tempData = new Array(1000).fill(0).map(() => ({
        id: Math.random(),
        data: new Array(100).fill(Math.random()),
      }));
      
             // Á≠âÂæÖ‰∏ÄÂ∞èÊÆµÊó∂Èó¥
       await new Promise<void>(resolve => setTimeout(resolve, 100));
      
      // Ê∏ÖÁêÜÂºïÁî®
      tempData.length = 0;
    }

    const finalMetrics = await deviceInfoManager.getCurrentPerformanceMetrics();
    const finalMemory = finalMetrics.memoryUsage.used;
    const memoryGrowth = finalMemory - initialMemory;

    return {
      initialMemory,
      finalMemory,
      memoryGrowth,
      averageGrowthPerIteration: memoryGrowth / iterations,
    };
  }

  /**
   * ËÆæÁΩÆÂ∫îÁî®Áä∂ÊÄÅÁõëÂê¨
   */
     private setupAppStateListener(): void {
     const handleAppStateChange = (nextAppState: AppStateStatus) => {
       if (nextAppState === 'background') {
         console.log('üì± Â∫îÁî®ËøõÂÖ•ÂêéÂè∞ÔºåÊöÇÂÅúÊÄßËÉΩÁõëÊéß');
         this.stopMonitoring();
       } else if (nextAppState === 'active') {
         console.log('üì± Â∫îÁî®ÂõûÂà∞ÂâçÂè∞ÔºåÊÅ¢Â§çÊÄßËÉΩÁõëÊéß');
         this.startMonitoring();
       }
     };
     
     AppState.addEventListener('change', handleAppStateChange);
   }

  /**
   * Ëé∑ÂèñÊÄßËÉΩÊä•Âëä
   */
  getPerformanceReport(): {
    alerts: PerformanceAlert[];
    memoryWarnings: MemoryWarning[];
    benchmarks: PerformanceBenchmark[];
    summary: {
      totalAlerts: number;
      criticalAlerts: number;
      memoryIssues: number;
      averageBenchmarkTime: number;
    };
  } {
    const completedBenchmarks = Array.from(this.benchmarks.values()).filter(b => b.duration !== undefined);
    const averageBenchmarkTime = completedBenchmarks.length > 0
      ? completedBenchmarks.reduce((sum, b) => sum + (b.duration || 0), 0) / completedBenchmarks.length
      : 0;

    return {
      alerts: [...this.performanceAlerts],
      memoryWarnings: [...this.memoryWarnings],
      benchmarks: completedBenchmarks,
      summary: {
        totalAlerts: this.performanceAlerts.length,
        criticalAlerts: this.performanceAlerts.filter(a => a.severity === 'critical').length,
        memoryIssues: this.memoryWarnings.length,
        averageBenchmarkTime,
      },
    };
  }

  /**
   * Ê∏ÖÈô§ÊâÄÊúâÊÄßËÉΩÊï∞ÊçÆ
   */
  clearPerformanceData(): void {
    this.performanceAlerts = [];
    this.memoryWarnings = [];
    this.benchmarks.clear();
    console.log('üßπ ÊÄßËÉΩÊï∞ÊçÆÂ∑≤Ê∏ÖÈô§');
  }

  /**
   * Ëé∑ÂèñÊÄßËÉΩ‰ºòÂåñÂª∫ËÆÆ
   */
  getOptimizationSuggestions(): string[] {
    const suggestions: string[] = [];
    const report = this.getPerformanceReport();

    // Âü∫‰∫éË≠¶ÂëäÁîüÊàêÂª∫ËÆÆ
    if (report.summary.memoryIssues > 0) {
      suggestions.push('Ê£ÄÊµãÂà∞ÂÜÖÂ≠ò‰ΩøÁî®ÈóÆÈ¢òÔºåÂª∫ËÆÆ‰ºòÂåñÂÜÖÂ≠òÁÆ°ÁêÜ');
      suggestions.push('ËÄÉËôë‰ΩøÁî®React.memo()ÂíåuseMemo()‰ºòÂåñÁªÑ‰ª∂Ê∏≤Êüì');
      suggestions.push('ÂèäÊó∂Ê∏ÖÁêÜ‰∏çÈúÄË¶ÅÁöÑ‰∫ã‰ª∂ÁõëÂê¨Âô®ÂíåÂÆöÊó∂Âô®');
    }

    if (report.summary.criticalAlerts > 0) {
      suggestions.push('Ê£ÄÊµãÂà∞‰∏•ÈáçÊÄßËÉΩÈóÆÈ¢òÔºåÂª∫ËÆÆÁ´ãÂç≥‰ºòÂåñ');
      suggestions.push('ËÄÉËôë‰ΩøÁî®ÊáíÂä†ËΩΩÂíå‰ª£Á†ÅÂàÜÂâ≤ÂáèÂ∞ëÂàùÂßãÂä†ËΩΩÊó∂Èó¥');
    }

    if (report.summary.averageBenchmarkTime > 1000) {
      suggestions.push('Êìç‰ΩúÂìçÂ∫îÊó∂Èó¥ËæÉÈïøÔºåÂª∫ËÆÆ‰ºòÂåñÁÆóÊ≥ïÂíåÊï∞ÊçÆÁªìÊûÑ');
      suggestions.push('ËÄÉËôë‰ΩøÁî®Web WorkersÂ§ÑÁêÜËÄóÊó∂Êìç‰Ωú');
    }

    // ÁΩëÁªúÁõ∏ÂÖ≥Âª∫ËÆÆ
    const networkAlerts = report.alerts.filter(a => a.type === 'network');
    if (networkAlerts.length > 0) {
      suggestions.push('ÁΩëÁªúÂª∂ËøüËæÉÈ´òÔºåÂª∫ËÆÆÂÆûÁé∞ËØ∑Ê±ÇÁºìÂ≠òÂíåÁ¶ªÁ∫øÂäüËÉΩ');
      suggestions.push('ËÄÉËôë‰ΩøÁî®CDNÂä†ÈÄüÈùôÊÄÅËµÑÊ∫êÂä†ËΩΩ');
    }

    // Ê∏≤ÊüìÁõ∏ÂÖ≥Âª∫ËÆÆ
    const renderAlerts = report.alerts.filter(a => a.type === 'render');
    if (renderAlerts.length > 0) {
      suggestions.push('Ê∏≤ÊüìÊÄßËÉΩ‰∏ç‰Ω≥ÔºåÂª∫ËÆÆ‰ºòÂåñÁªÑ‰ª∂ÁªìÊûÑÂíåÂáèÂ∞ëÈáçÊ∏≤Êüì');
      suggestions.push('‰ΩøÁî®FlatListÊõø‰ª£ScrollViewÂ§ÑÁêÜÂ§ßÈáèÊï∞ÊçÆ');
    }

    return suggestions;
  }
}

